import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const WebPage = () => import('../pages/WebPage.vue')
const WebBasePage = () => import('../pages/web/WebBasePage.vue')
const WebVisPage = () => import('../pages/web/WebVisPage.vue')
const VuePage = () => import('../pages/VuePage.vue')
const ReactPage = () => import('../pages/ReactPage.vue')
const AngularPage = () => import('../pages/AngularPage.vue')
const WeappPage = () => import('../pages/WeappPage.vue')
const ResPage = () => import('../pages/other/ResPage.vue')
const LinksPage = () => import('../pages/other/LinksPage.vue')

export default new Router({
  routes: [
    {
      path: '',
      redirect: '/vue'
    },
    {
      path: '/webnav',
      redirect: '/vue'
    },
    {
      path: '/web',
      component: WebPage,
    },
    {
      path: '/web-base',
      component: WebBasePage
    },
    {
      path: '/web-vis',
      component: WebVisPage
    },
    {
      path: '/vue',
      component: VuePage
    },
    {
      path: '/react',
      component: ReactPage
    },
    {
      path: '/angular',
      component: AngularPage
    },
    {
      path: '/weapp',
      component: WeappPage
    },
    {
      path: '/other-res',
      component: ResPage
    },
    {
      path: '/other-links',
      component: LinksPage
    },
  ],
  mode: 'history'
})



