</template>

<script>
export default {
  name: 'PageHeader'
}
</script>

<style>
.header-container {
  display: flex;
  background-color: #001938;
  padding: 0 20px;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 1;
  background-color: #001938;
  width: 100%;
}

.icon-style {
  height: 60px;
}

.website-title {
  color: #fff;
  font-family: "PingFang SC"; 
  font-size: 25px;
  margin-left: 30px;
  line-height: 60px;
}
</style>


