<template>
  <el-menu
    :router="true"
    default-active="/vue"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    border-right="none"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b">
    <el-submenu index="/web">
      <template slot="title">
        <img src="@/assets/img/nav_menu/web.png" class="nav_img">
        <span>web开发</span>
      </template>
      <el-menu-item index="/web-base">web开发基础</el-menu-item>
      <el-menu-item index="/web-vis">可视化</el-menu-item>
    </el-submenu>    
    <el-menu-item index="/vue">
      <img src="@/assets/img/nav_menu/vue.png" class="nav_img">
      <span slot="title">Vue.js</span>
    </el-menu-item>
    <el-menu-item index="/react">
      <img src="@/assets/img/nav_menu/react.png" class="nav_img">
      <span slot="title">React.js</span>
    </el-menu-item>
    <el-menu-item index="/angular">
      <img src="@/assets/img/nav_menu/angular.png" class="nav_img">
      <span slot="title">Angular.js</span>
    </el-menu-item>
    <el-menu-item index="/weapp">
      <img src="@/assets/img/nav_menu/weapp.svg" class="nav_img">
      <span slot="title">小程序</span>
    </el-menu-item>
    <el-submenu index="other">
      <template slot="title">
        <i class="el-icon-menu"></i>
        <span>其它</span>
      </template>
      <el-menu-item index="other-res">资源</el-menu-item>
      <el-menu-item index="other-links">友情链接</el-menu-item>
    </el-submenu>
  </el-menu>
</template>

<script>
export default {
  name: 'NavMenu',
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style>
.el-menu {
  border-right: none;
}
.nav_img {
  height: 24px;
  margin-right: 5px;
}
</style>


