<template>
  <div id="app">
    <el-container>
      <el-header style="padding: 0">
        <page-header></page-header>
      </el-header>
      <el-container>
        <el-aside style="background-color: #545c64; position: fixed; height: 100%;">
          <nav-menu></nav-menu>
        </el-aside>
        <el-container direction="vertical">
          <el-main style="background-color: #f9f9f9; margin-left: 300px;min-height: calc(100vh - 120px);">
            <!-- <main-content></main-content> -->
            <router-view></router-view>
          </el-main>
          <el-footer style="height: 48px">
            <page-footer></page-footer>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import PageFooter from '@/components/PageFooter'
import NavMenu from '@/components/NavMenu'
import MainContent from '@/components/MainContent'
import VuePage from '@/pages/VuePage'

export default {
  name: 'App',
  components: {
    PageHeader,
    PageFooter,
    NavMenu,
    MainContent,
    VuePage
  }
}
</script>

<style>
